@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Lato", sans-serif;
    color: #fff;
}

html {
    background-image: url(/crypto-search/static/media/wave.b2114ffc.png);
    background-position: top left;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: contain;
    background-color: #1a1a1c;
}

.coin-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
}

.coin-app-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
}

.coin-search {
    margin: 40px 0 64px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.coin-text {
    margin: 32px 0;
    text-align: center;
}

.coin-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(-225deg, #b91884 0%, #ee2852 48%, #f17791 100%);
    color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
    color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
    color: #e2e2e2;
}

.coin-input::placeholder {
    color: #e2e2e2;
}

.blinker {
    -webkit-animation-name: blinker;
            animation-name: blinker;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: cubic-bezier(1, 0, 0, 1);
            animation-timing-function: cubic-bezier(1, 0, 0, 1);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    display: inline-block;
    width: 4px;
    height: 50px;
    margin-left: 10px;
    border-radius: 3px;
    box-shadow: 0 0 10px rgba(white, 0.3);
    background: #ee2852;
}

@-webkit-keyframes blinker {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes blinker {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@media (max-width: 1100px) {
    html {
        background-image: none;
    }
}

.coin-container {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin h1:first-child {
    margin-left: 40px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-around;
    width: 100%;
}

.coin-price {
    width: 110px;
}

.coin-volume {
    width: 200px;
}

.coin-marketcap {
    width: 240px;
}

.coin-percent {
    width: 20%;
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}

.bold {
    font-weight: bold;
}

@media (max-width: 900px) {
    .coin h1 {
        width: auto;
        width: initial;
    }

    .coin-row {
        justify-content: space-around;
    }
    .coin-volume {
        display: none;
    }
    .coin-price {
        width: auto;
    }

    .coin-marketcap {
        display: none;
    }

    .coin-row {
        width: 500px;
    }
    .coin {
        justify-content: space-around;
        min-width: 200px;
        padding-right: 0px;
    }
}

